.mp-toc__item {
  font-size: 1rem;
  padding-top: 0.35rem;
  padding-bottom: 0.35rem;
}
.mp-toc__item a.mp-toc__item__link {
  font-weight: 400;
  color: #222;
}
.mp-toc__item a.mp-toc__item__link:active,
.mp-toc__item a.mp-toc__item__link:hover {
  color: #198ae6;
}
.mp-toc__item a.mp-toc__item__link--active {
  font-weight: 600;
  color: #198ae6;
}
.mp-toc__item[data-toc-item-level="1"] {
  margin-left: 0;
  font-size: 1.1rem;
  font-weight: 700;
  margin-block-end: 25px;
}
.mp-toc__item[data-toc-item-level="2"] {
  margin-left: 0;
  font-size: 1rem;
}
.mp-toc__item[data-toc-item-level="3"] {
  margin-left: 20px;
  font-size: 0.9rem;
}
.mp-toc__item[data-toc-item-level="4"] {
  margin-left: 30px;
}
.mp-toc__item[data-toc-item-level="5"] {
  margin-left: 40px;
}
.mp-toc__item[data-toc-item-level="6"] {
  margin-left: 50px;
}
