body,
html {
  padding: 0;
  margin: 0;
  background-color: #fff;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
}
a,
a:visited,
a:active {
  font-weight: 500;
  text-decoration: none;
  color: #728798;
}
a:hover {
  color: #4f5f6c;
  text-decoration: none;
}
pre {
  margin: 0;
}
code[class*="language-"]::-webkit-scrollbar {
  display: none;
  padding: 0;
  margin: 0;
}
pre[class*="language-"]::-webkit-scrollbar {
  display: none;
}
code[class*="language-"],
pre[class*="language-"] {
  scrollbar-width: none;
  overflow: auto;
  color: #fff;
  font-family: Consolas, monaco, monospace !important;
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  word-wrap: normal;
  line-height: 1.5;
  font-weight: 600;
  -moz-tab-size: 4;
  -o-tab-size: 4;
  tab-size: 4;
  -webkit-hyphens: none;
  -ms-hyphens: none;
  hyphens: none;
  background: transparent;
  color: #fff;
}
code[class*="language-"]::-moz-selection,
code[class*="language-"] ::-moz-selection,
pre[class*="language-"]::-moz-selection,
pre[class*="language-"] ::-moz-selection {
  text-shadow: none;
  background: #4373c2;
}
code[class*="language-"]::selection,
code[class*="language-"] ::selection,
pre[class*="language-"]::selection,
pre[class*="language-"] ::selection {
  text-shadow: none;
  background: #4373c2;
}
@media print {
code[class*='language-'],
  pre[class*='language-'] {
    text-shadow: none;
}
}
.saber-highlight {
  position: relative;
  overflow: hidden;
  margin: 2rem 0;
  color: #fff;
  background-color: #2d3748;
  border-radius: 8px;
}
.saber-highlight:before {
  background: rgba(201,167,255,0.2);
  color: var(--text-color);
  border-radius: 0 0 8px 8px;
  top: 0;
  padding: 0 10px;
}
.code-line.highlighted {
  background: rgba(201,167,255,0.2);
  box-shadow: inset 3px 0 0 0 #c9a7ff;
  background: rgba(3,169,244,0.121);
}
.saber-highlight-mask,
.saber-highlight-code {
  line-height: 1.5;
  background-color: transparent !important;
  text-shadow: none !important;
  box-shadow: none !important;
  margin: 0 !important;
  padding-top: 1rem !important;
  padding-bottom: 1rem !important;
  padding-left: 1rem !important;
  padding-right: 1rem !important;
  white-space: pre;
  font-family: Menlo, Monaco, Consolas, Liberation Mono, Courier New, monospace !important;
}
.saber-highlight-mask {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1;
  color: transparent !important;
  padding-left: 0 !important;
  padding-right: 0 !important;
}
.saber-highlight-code {
  position: relative;
  z-index: 2;
  font-size: 0 !important;
  font-family: Menlo, Monaco, Consolas, Liberation Mono, Courier New, monospace !important;
}
.saber-highlight-code code,
.saber-highlight-mask {
  font-size: 0.875rem;
}
.saber-highlight-line-numbers {
  pointer-events: none;
  font-size: 100%;
  float: left;
  letter-spacing: -1px;
  border-right: 1px solid #999;
  user-select: none;
  text-align: right;
  padding-right: 0.8rem;
  margin-right: 0.8rem;
  counter-reset: linenumber;
}
.saber-highlight-line-numbers > span {
  counter-increment: linenumber;
  display: block;
}
.saber-highlight-line-numbers > span:before {
  content: counter(linenumber);
  color: #999;
  display: block;
}
.token.cdata,
.token.comment,
.token.prolog {
  color: #637777;
  font-style: italic;
}
.token.punctuation {
  color: #93ddfd;
}
.token.deleted {
  color: rgba(239,83,80,0.56);
  font-style: italic;
}
.token.boolean {
  color: #ff5874;
}
.token.number {
  color: #f78c6c;
}
.token.italic {
  font-style: italic;
}
.namespace {
  color: #b2ccd6;
}
.token.property,
.token.symbol {
  color: #80cbc4;
}
.token.keyword,
.token.operator,
.token.tag {
  color: #ff8383;
}
.token.builtin,
.token.char,
.token.constant,
.token.function {
  color: #82aaff;
}
.token.doctype,
.token.selector {
  color: #c792ea;
}
.token.attr-name,
.token.inserted {
  color: #ffe484;
}
.language-css .token.string,
.style .token.string,
.token.entity,
.token.string,
.token.url {
  color: #addb67;
}
.token.atrule,
.token.attr-value,
.token.class-name {
  color: #b5f4a5;
}
.token.important,
.token.regex,
.token.variable {
  color: #d6deeb;
}
.token.bold,
.token.important {
  font-weight: 700;
}
