.page-content {
  line-height: 1.9rem;
  color: #000;
}
.page-content img {
  max-width: 100%;
}
.page-content p {
  margin: 1rem 0;
  line-height: 1.9rem;
}
.page-content hr {
  border: none;
  height: 1px;
  background: #efefef;
  margin: 30px 0;
}
.page-content p code,
.page-content td code,
.page-content blockquote code,
.page-content li code {
  background-color: rgba(188,216,255,0.2);
  padding-left: 0.3rem;
  padding-right: 0.3rem;
  padding-top: 0.3rem;
  padding-bottom: 0.3rem;
  font-size: 0.9rem;
  border-radius: 4px;
  color: rgba(0,49,180,0.9);
}
.page-content pre {
  padding-top: 2rem;
  padding-bottom: 2rem;
}
.page-content pre code {
  margin: 0;
  padding: 0;
  background-color: transparent;
}
.page-content ol {
  list-style-type: decimal;
}
.page-contentol,
.page-content ul {
  padding: 0;
  margin: 0rem;
  list-style: none;
}
.page-content li {
  padding-left: 0.6rem;
}
.page-content ul > li::before {
  content: "—";
  display: inline-block;
  color: #b3b3b3;
  position: absolute;
  margin-left: -1.5rem;
}
.page-content ul.task-list > li::before {
  content: "";
  display: inline-block;
  position: absolute;
}
.page-content ul.task-list > li > label,
.page-content ul.task-list input {
  cursor: pointer;
}
.page-content ol.task-list,
.page-content ul.task-list {
  list-style: none;
  padding-left: 0;
  font-size: 0.9rem;
  line-height: 1.4rem;
}
blockquote {
  background-color: rgba(255,229,100,0.3);
  border-left: 8px solid #ffe564;
  padding: 15px 30px 15px 15px;
  margin: 2rem 0;
  color: #6a737d;
}
blockquote > :first-child {
  margin-top: 0;
}
blockquote > :last-child {
  margin-bottom: 0;
}
small code {
  font-size: inherit;
}
table {
  border-collapse: separate;
  border-spacing: 0;
  width: 100%;
}
table thead th td {
  height: 2.857142857142857rem;
}
table tbody tr td {
  height: 3.571428571428572rem;
}
table th {
  height: 2.857142857142857rem;
  color: #666;
  font-size: 1rem;
  font-weight: 400;
  letter-spacing: 0;
  background: #fafafa;
}
table th:first-child {
  border-left: 1px solid #eaeaea;
  border-radius: 4px 0 0 4px;
}
table th:last-child {
  border-bottom: 1px solid #eaeaea;
  border-radius: 0 4px 4px 0;
  border-right: 1px solid #eaeaea;
  border-top: 1px solid #eaeaea;
}
table tr td {
  border-bottom: 1px solid #eaeaea;
  color: #444;
  font-size: 1rem;
  height: 2.857142857142857rem;
}
table td:first-child {
  border-left: 1px solid transparent;
}
ul.task-list {
  list-style: none;
  padding-left: 0;
}
.page-content table td,
table th {
  padding: 0 0.714285714285714rem;
  text-align: left;
}
.page-content table th,
table th:first-child {
  border-bottom: 1px solid #eaeaea;
  border-top: 1px solid #eaeaea;
}
.mp-layout {
  padding-top: 3.6rem;
  padding-bottom: 50px;
}
.mp-layout__left__container {
  overflow-y: scroll;
  position: fixed;
  width: 320px;
  top: 64px;
  bottom: 0;
  left: 0;
  border-right: 1px solid #dce1e5;
}
.mp-layout__left {
  padding-left: 2rem;
  padding-right: 2rem;
  padding-top: 2rem;
  padding-bottom: 2rem;
}
.mp-layout__nav {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}
.mp-layout__container {
  margin-left: calc(320px + 2rem);
  padding-top: 40px;
  padding-left: 2rem;
  padding-right: 2rem;
}
