.mp-nav__item {
  margin-left: 2rem;
  line-height: 2.5rem;
}
.mp-nav__item a {
  line-height: 1.4rem;
  color: inherit;
}
.mp-nav__item a.mp-nav__item__link {
  color: #3e4348;
  padding-bottom: 8px;
}
.mp-nav__item a.mp-nav__item__link:hover {
  margin-bottom: -$navItemLinkUnderlineWidth;
  border-bottom: 2.5px solid #728798;
  padding-bottom: 8px;
}
.mp-nav__item a.mp-nav__item__link--active {
  margin-bottom: -$navItemLinkUnderlineWidth;
  border-bottom: 2.5px solid #728798;
}
